import Vue from 'vue'
import Router from 'vue-router'
import Error from '@/components/Error'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/login',
      name: 'login',
      component: (resolve) => {require(['views/Login'], resolve)},
    },
    {
      path: '/',
      component: (resolve) => { require(['views/Main'], resolve) }, 
      meta: { requireAuth: true },
      children: [
        {
          path: 'Welcome',
          component: (resolve) => {require(['views/Welcome'], resolve)},
          name: 'Welcome'
        },
        {
          path: 'repswd',
          component: (resolve) => {require(['views/User/RePswd'], resolve)},
          name: '修改密码'
        },
        { 
          path: 'UserInfo',
          component: (resolve) => {require(['views/User/UserInfo'], resolve)},
          name: '用户信息'
        },
        { 
          path: 'commentM',
          component: (resolve) => {require(['views/Comment/Comment'], resolve)},
          name: '评论管理'
        },
        {
          path: 'Reply',
          component: (resolve) => {require(['views/Comment/Reply'], resolve)},
          name: '评论回复'
        },
        {
          path: 'userM',
          component: (resolve) => {require(['views/User/User'], resolve)},
          name: '用户管理'
        },
        {
          path: 'userC',
          component: (resolve) => {require(['views/User/UserCheck'], resolve)},
          name: '作者审核'
        },
       
        {
          path: 'comicM',
          component: (resolve) => {require(['views/Comics/ComicsM'], resolve)},
          name: '漫画管理'
        },
        {
          path: 'sectionM',
          component: (resolve) => {require(['views/Comics/SectionM'], resolve)},
          name: '漫画章节'
        },
        {
          path:'*',
          component:Error,
          name: '错误请求'
        },    
      ]
    }
  ]
})



