export default{
  install(Vue,options)
  {
    Vue.prototype.getMsg = function (_this,response) {
      if(0 === response.data.code){
        _this.tableData = response.data.data.content
        _this.totalCount = response.data.data.totalElements
      }else if(500 === response.data.code){
        _this.$message({message: response.data.msg,type: 'error'})
        sessionStorage.removeItem('BIGDATA_PLATFORM')
        setTimeout(function () {
          _this.$router.push({ path: '/Welcome' })
        }, 500)
      }else{
        _this.$message({message: response.data.msg,type: 'error'})
      }
    },
    Vue.prototype.callMsg = function (_this,response) {
      if(0 === response.data.code){
        _this.$message({message: response.data.data,type: 'success'})
      }else if(500 === response.data.code){
        _this.$message({message: response.data.msg,type: 'error'})
        sessionStorage.removeItem('BIGDATA_PLATFORM')
        setTimeout(function () {
          _this.$router.push({ path: '/Welcome' })
        }, 500)
      }else{
        _this.$message({message: response.data.msg,type: 'error'})
      }
    }
  }
}


